import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule, HttpClient } from "@angular/common/http";
import { TranslateHttpLoader } from "@ngx-translate/http-loader";
import { TranslateModule, TranslateLoader } from "@ngx-translate/core";

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { AboutComponent } from './components/about/about.component';
import { ContactComponent } from './components/contact/contact.component';
import { CookieBarComponent } from './components/cookie-bar/cookie-bar.component';
import { EasterEggModalComponent } from './components/easter-egg-modal/easter-egg-modal.component';
import { LangButtonComponent } from './components/lang-button/lang-button.component';
import { FooterComponent } from './components/footer/footer.component';
import { LegalFooterComponent } from './components/legal-footer/legal-footer.component';
import { PassionsComponent } from './components/passions/passions.component';
import { ServicesComponent } from './components/services/services.component';
import { ThemeButtonComponent } from './components/theme-button/theme-button.component';
import { HomeComponent } from './pages/home/home.component';
import { NotFoundComponent } from './pages/not-found/not-found.component';
import { InstaComponent } from './pages/insta/insta.component';
import { LegalComponent } from './pages/legal/legal.component';

export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, "./assets/i18n/", ".json");
}

@NgModule({
  declarations: [
    AppComponent,
    AboutComponent,
    ContactComponent,
    CookieBarComponent,
    EasterEggModalComponent,
    LangButtonComponent,
    FooterComponent,
    LegalFooterComponent,
    PassionsComponent,
    ServicesComponent,
    ThemeButtonComponent,
    HomeComponent,
    NotFoundComponent,
    InstaComponent,
    LegalComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    })
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
