import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'cookie-bar',
  templateUrl: './cookie-bar.component.html',
  styleUrls: ['./cookie-bar.component.css']
})
export class CookieBarComponent implements OnInit {
  public isVisible: boolean = true;

  private COOKIE_ACCEPT_KEY = "cookie-accept";

  constructor() { }

  ngOnInit() {
    if (localStorage.getItem(this.COOKIE_ACCEPT_KEY)) this.isVisible = false;
  }

  close() {
    this.isVisible = false;
    localStorage.setItem(this.COOKIE_ACCEPT_KEY, new Date().toISOString());
  }
}
