import { Component, OnInit } from '@angular/core';

declare var $: any;

@Component({
  selector: 'passions',
  templateUrl: './passions.component.html',
  styleUrls: ['./passions.component.css']
})
export class PassionsComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    const color = "#a8e063";
    $("#wmap").vectorMap({
      backgroundColor: "transparent",
      map: "world_mill",
      series: {
        regions: [
          {
            values: {
              CA: color,
              FR: color,
              BR: color,
              GB: color,
              US: color,
              ES: color,
              LU: color,
              BE: color,
              CH: color,
              IT: color
            }
          }
        ]
      },
      regionStyle: {
        initial: { "stroke-width": 0.5, fill: "#e5e5e5" }
      },
      hoverOpacity: 0.7,
      hoverColor: false,
      zoomOnScroll: false,
      zoomButtons: false
    });
  }
}
