import { Component, HostListener } from "@angular/core";

import { ThemeService } from "./services/theme.service";
import { LangService } from "./services/lang.service";
import * as AOS from "aos";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.css"]
})
export class AppComponent {
  private konamiCode: number[] = [38, 38, 40, 40, 37, 39, 37, 39, 66, 65];
  private keyIndex: number = 0;
  public easterEgg: boolean = false;

  constructor(
    public langService: LangService,
    public themeService: ThemeService
  ) {
    langService.init();

    AOS.init();
  }

  @HostListener("window:keydown", ["$event"])
  keyListen(e: KeyboardEvent) {
    if (e.keyCode == this.konamiCode[this.keyIndex++]) {
      if (this.keyIndex === this.konamiCode.length) {
        this.keyIndex = 0;
        this.easterEgg = true;
        setTimeout(() => {
          this.themeService.pink();
          this.easterEgg = false;
          let audio = new Audio();
          audio.src = window.location.pathname + "assets/wow.mp3";
          audio.load();
          audio.play();
        }, 1650);
      }
    } else {
      this.keyIndex = 0;
    }
  }
}
